//sends the message to AWS
import { appendRawMessagesAndReCalc,setMostRecentSentMsg } from "./MessageStore";
import { createMessage,deleteMessage } from "../graphql/mutations";
import { API,graphqlOperation } from 'aws-amplify';
import { formatCDate } from "../util/DateFormatUtil";
import { shouldMockSendMessagesToAWS } from "../environment";

export const sendMessageToAWS = async (employerId, msg) => {
    //fill in other fields
    //aws will set id,createdAt,updatedAt
    msg.channelID = employerId;
    msg.cdate = formatCDate();

    console.log("sendMessageToAWS ====> ");
    console.log(msg);
    let mockFlag = shouldMockSendMessagesToAWS(); //when we run the react app locally you need to set this to true

    if (mockFlag) {
        console.log("mockFlag true");
        //don't actually send, just append to our local msg store
        msg.id = createRandomStr(); //simulate id with a random str
        msg.createdAt = createNowDateStr();
        msg.updatedAt = createNowDateStr();
        //msg.haveRead = true;
        setMostRecentSentMsg(msg);
        let ar = [msg];
        //appendRawMessages(ar);
        appendRawMessagesAndReCalc(ar);
    } else {
        setMostRecentSentMsg(msg);
        const newObj = await API.graphql(graphqlOperation(createMessage, { input: msg }));
        console.log("backFromAWS");
    }
}

export const deleteMessageOnAWS = async ( msg) => {
    console.log("deleteMessageOnAWS ====> ");
    console.log(msg);

    let mockFlag = shouldMockSendMessagesToAWS(); //when we run the react app locally you need to set this to true

    if (mockFlag) {
        //do nothing
    } else {
        const newObj = await API.graphql(graphqlOperation(deleteMessage, 
            { input: { id: msg.id } }));
    
        console.log("backFromAWS");
    }
}


const createRandomStr = () => {
    let anysize = 3;//the size of string 
    let charset = "abcdefghijklmnopqrstuvwxyz"; //from where to create
    let result = "R";
    for( var i=0; i < anysize; i++ ) {
        result += charset[Math.floor(Math.random() * charset.length)];
    }
    return result;
}

const createNowDateStr = () => {
    let dt = new Date();
    let str = dt.toISOString();
    return str;
}

