import React, { useState, useEffect } from "react";

export const ImpersonateUserSelector = ({currentLoggedInUserId, setCurrentLoggedInUserId, allUsers, impersonatedUser, setImpersonatedUser}) => {
    const [selected, setSelected] = useState('none');
    const [actualLoggedInUserId, setActualLoggedInUserId] = useState(null);

    useEffect(() => {
        console.log("-- ImpersonateUserSelector --");
        setActualLoggedInUserId(currentLoggedInUserId);
    }, []);

  //--click--
  const onSelectOne = (userId) => {
    console.log("select: " + userId);
    if (userId == "none") {
        setImpersonatedUser(null);
        setCurrentLoggedInUserId(actualLoggedInUserId);
        setSelected('none');
        return;
    }
    const result = allUsers.filter((x) => x.id === userId);
    //console.log("xxx " + result.length);
    if (result.length > 0) { //exists already in store?
        let user = result[0];
        setImpersonatedUser(user);
        setCurrentLoggedInUserId(user.id);
        setSelected(user.id);
    }
  }
    
  //--calc--

  //--render--
  const renderImpersonation = () => {
    if (impersonatedUser == null) {
        return <div>Impersonation is <b>OFF</b></div>;
    }

    return <>
    <div>Impersonating: <b>{impersonatedUser.userName}</b> {impersonatedUser.id}</div>
    </>
  }
  const renderUsers = () => {
    if (allUsers == null) return;

    const resultAr = allUsers.filter((x) => x.id != actualLoggedInUserId);

    return <>
        {resultAr.map((user) => (
             <option key={user.id} value={user.id}>{user.userName}: {user.id}</option>
        ))}
    </>
  }
  
  return <div className="mt-2 mb-3">
    {renderImpersonation()}
    <div>Select a user to impersonate </div>
    <div className="select">
    <select onChange={e => onSelectOne(e.target.value)} defaultValue={selected}>
        <option value="none">None</option>
        {renderUsers()}
    </select>
    </div>
  </div>;
}


