import React, { useState, useEffect } from "react";
import { appendRawMessagesAndReCalc,deleteRawMessage } from "./MessageStore";
import { onCreateMessage, onDeleteMessage } from "../graphql/subscriptions";
import { API,graphqlOperation } from 'aws-amplify';

export const MessageReceiver = ({employer, incMsgCounter, debugFlag}) => {
    const [value, setValue] = useState(null);
    const [random, setRandom] = useState(0); //used to force rerender when msg sent or arrives

    useEffect(() => {
        console.log("setup AWS Msg subscription................................................: " + employer.id)

        //TODO subscribe filter only our channelID
//        let xx = '75b68615-9124-468d-ab55-eae6f12db545';
        const filter = {channelID: {eq: employer.id}};

        let subscription = API.graphql(
            graphqlOperation(onCreateMessage, { filter:filter })
        ).subscribe({
            next: ({ provider, value }) => {
                let msg = value.data.onCreateMessage;
                //hmm. if create msg in appsync it arrives as value.data.createMessage
                console.log("GOT ONE");
                console.log(msg);
                msg.haveRead = false;
                let ar = [msg];
                appendRawMessagesAndReCalc(ar);
                //Note. doesn't work calling incMsgCounter() here. 
                //So create random number (since any react state var value is static within this anon fn)
                let x = Math.random() * 10000;
                setRandom(x);
                //console.log(x);
            },
            error: (error) => console.warn(error),
        });

        setValue(subscription);

        //and do the 2nd subscription (called when message is deleted)
        let subscription2 = API.graphql(
            graphqlOperation(onDeleteMessage, { filter:filter })
        ).subscribe({
            next: ({ provider, value }) => {
                let msg2 = value.data.onDeleteMessage;
                //hmm. if create msg in appsync it arrives as value.data.createMessage
                console.log("GOT ONE MSG DEL");
                console.log(msg2);

                deleteRawMessage(msg2);
                //Note. doesn't work calling incMsgCounter() here. 
                //So create random number (since any react state var value is static within this anon fn)
                let x = Math.random() * 10000;
                setRandom(x);
            },
            error: (error) => console.warn(error),
        });

        }, []);

    //runs whenever random changes
    useEffect(() => {
            console.log("msg-receive.random " + random);
            if (random != 0) {
                incMsgCounter(); //force re-render
            }
    }, [random]);
    

    //--click--
      
  //--render--
  return <>
   {debugFlag && (
    <h4>Receiver</h4>
   )}
  </>;
}


