

export const sortUserList = (viewedUsers) => {
    //don't sort first user (should always be at the top)
    if (viewedUsers.length > 0) {
      let ar = [];
      for (let i = 1; i < viewedUsers.length; i++) {
        ar[i] = viewedUsers[i];
      }

      ar.sort((a, b) => a.userName.toLowerCase().localeCompare(b.userName.toLowerCase()));
      console.log("!after sortx!");
      console.log(ar);
      ar.splice(0, 0, viewedUsers[0]); //insert at front
      return ar;
    } else {
      return viewedUsers;
    }
  }
