import React, { useState, useEffect } from "react";


export const YesNoDialog = ({active, setActive, title, messageText, doAction}) => {
    //--click--
    const clkOK = () => {
        setActive(false);
        doAction();
    }

  //--calc--
  const calcActive2 = () => {
    if (active) return "modal is-active";
    else return "modal";
  }


  //--render--

  return <>
  <div className="container" id="app">
  <div id="image-modal" className={calcActive2()}>
    <div className="modal-background"></div>
    <div className="modal-content">
     <div className="modal-card">
    <header className="modal-card-head">
      <p className="modal-card-title">{title}</p>
    </header>
    <section className="modal-card-body">
      <b>{messageText}</b>
    </section>
    <footer className="modal-card-foot">
      <button className="button is-success" onClick={clkOK}>OK</button>
      <button className="button" onClick={() => setActive(false)}>Cancel</button>
    </footer>
  </div>
    </div>
    <button id="image-modal-close" className="modal-close" onClick={() => setActive(false)}></button>
  </div>
  </div>

  </>;
}


