import React, { useState, useEffect, useRef } from "react";
import {
  findMessagesForUser,
  findMessagesForUserInGroupConversation,
} from "../state/MessageStore";
import {
  formatTimeOfDay,
  formatDateForDivider,
  areSameDate,
} from "../util/DateFormatUtil";
import { MessageEditor } from "./MessageEditor";
import {
  calcMonthToLoad,
  loadMessagesForUserAndMonth,
  haveReachedMaxHistory,
} from "../state/MessageHistoryLoader";
import { faTrash } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { YesNoDialog } from "./helper/YesNoDialog";
import { deleteMessageOnAWS } from "../state/MessageSender";
import { TO_EVERYONE } from "../state/MsgType";
import { umClearUnreadCount } from "../state/UnreadMsgTracker";

export const MessagePane = ({
  selectedUser,
  currentLoggedInUserId,
  msgCounter,
  allUsers,
  employer,
  isEmployer,
  incMsgCounter,
  debugFlag,
  unreadClearedCounter,
  setUnreadClearedCounter,
}) => {
  const [messages, setMessages] = useState([]);
  const [maxMsgsToShow, setMaxMsgsToShow] = useState(20);
  const [areShowingAllLoadedMsgs, setAreShowingAllLoadedMsgs] = useState(false);
  const [mostRecentUserId, setMostRecentUserId] = useState(null);
  const [active, setActive] = useState(false);
  const [doomedMsg, setDoomedMsg] = useState(null);
  const bottomRef = useRef(null);
  //const [autoScroll, setAutoScroll] = useState(false);

  useEffect(() => {
    var ar = calcNumMsgsToShow();
    console.log("loadmsgs from store ", maxMsgsToShow, "len: " + ar.length);

    if (ar.length > maxMsgsToShow) {
      ar = ar.slice(ar.length - maxMsgsToShow);
      setAreShowingAllLoadedMsgs(false);
    } else {
      setAreShowingAllLoadedMsgs(true);
    }

    if (mostRecentUserId != selectedUser.id) {
      setMaxMsgsToShow(20); //reset if we're viewing a different user
    }
    setMostRecentUserId(selectedUser.id);

    for (let i = 0; i < ar.length; i++) {
      let msg = ar[i];
      msg.fromUserName = calcUserName(msg.fromUser);
      msg.toUserName = calcUserName(msg.toUser);
    }

    //console.log(ar);
    setMessages(ar);
  }, [selectedUser, msgCounter, maxMsgsToShow]);

  //auto-scrolling to bottom
  useEffect(() => {
    // console.log("useeffect2 ");

    const timeoutId = setTimeout(() => {
      // console.log("scrooooooooooooooooooo");
      bottomRef.current.scrollIntoView({
        behavior: "smooth",
        block: "start",
      });
    }, 200);

    return () => clearTimeout(timeoutId);
  }, [selectedUser]);

  const calcNumMsgsToShow = () => {
    var ar = null;
    if (selectedUser.id == TO_EVERYONE) {
      ar = findMessagesForUserInGroupConversation(selectedUser.id);
    } else {
      ar = findMessagesForUser(currentLoggedInUserId, selectedUser.id);
    }
    return ar;
  };
  //employees are not allowed to send messages on TO_EVERYONE channel
  const canSendMessage = () => {
    var b = false;
    if (selectedUser.id == TO_EVERYONE) {
      return isEmployer;
    } else {
      b = true;
    }
    return b;
  };

  //--click--
  const clkLoadHistory = async () => {
    let ar = calcNumMsgsToShow();
    console.log("xmcbs " + ar.length + ", " + messages.length);
    if (ar.length > messages.length) {
      setMaxMsgsToShow(ar.length);
      return; //can just view more msgs in messages
    }

    let cdate = calcMonthToLoad(selectedUser.id);
    if (cdate == null) {
      return; //no more messages
    }
    let n = await loadMessagesForUserAndMonth(selectedUser.id, cdate);
    console.log("n = " + n);
    setMaxMsgsToShow(maxMsgsToShow + 1); //force useEffect to run again
  };
  const clkDelete = (msg) => {
    console.log("FFFF " + msg.id);
    setActive(true);
  };
  const doDeleteMessage = async () => {
    console.log("doDeleteMessage " + doomedMsg.id);
    deleteMessageOnAWS(doomedMsg);
  };
  const calcUserName = (userId) => {
    let userName = "?";
    const result = allUsers.filter((x) => x.id === userId);
    //console.log("xxx " + result.length);
    if (result.length > 0) {
      //exists already in store?
      let tmp = result[0];
      return tmp.userName;
    }
    return userName;
  };
  const clkOnMsgs = () => {
    //console.log("onmmmmmmmx");
    umClearUnreadCount(currentLoggedInUserId, selectedUser.id); //clear any unread msgs
    setUnreadClearedCounter(unreadClearedCounter + 1);
  };

  //--calc--
  const calcTimeOfDay = (msg) => {
    let str = formatTimeOfDay(msg.updatedAt);
    //console.log(msg.updatedAt + " -> " + str);
    return str;
  };
  const calcUserNameSpan = (msg) => {
    return <span className="has-text-weight-bold">{msg.fromUserName}</span>;
  };
  const calcMoreMsgsCouldBeShown = () => {
    let b = haveReachedMaxHistory(selectedUser.id);
    return !b;
  };

  //--render--
  const renderDateDivider = (msg, dividerInfo) => {
    let dtStr = msg.updatedAt;

    if (dividerInfo.prev) {
      if (areSameDate(dividerInfo.prev, dtStr)) {
        return null;
      }
    }

    dividerInfo.prev = dtStr;
    let today = new Date().toISOString();
    let str = null;
    if (areSameDate(today, dtStr)) {
      str = "Today";
    } else {
      str = formatDateForDivider(dtStr);
    }

    // let lineStyle = {
    //     position: 'abosolute',
    //     left: 0,
    //     top: 0,
    //     borderRadius:'25px',
    //     borderWidth:'3px',
    //     borderStyle:'solid',
    //     borderColor: 'red',
    //     backgroundClip:'padding-box',
    //     backgroundColor: 'red',
    //     zIndex: -1
    // }

    return (
      <>
        <div className="date-wise-chat">
          <div
            className="tag is-white"
          >
            {str}
          </div>
        </div>
      </>
    );
  };

  //only employers get the delete button
  const renderDeleteButton = (msg) => {
    if (!isEmployer) return null;
    //remove the Delete-Msg feature for now. Perhaps implement later
    //return <FontAwesomeIcon icon={faTrash} onClick={() => clkDelete(msg)} />
    return null;
  };

  //--render--
  const renderMessage = (msg, index) => {
    let bgCol = "#E9F0F2";
    let marginLeft = "0%";
    let textColor = "#4a4a4a";
		
		// For right side chat only
    if (msg.fromUser == currentLoggedInUserId) {
      // console.log("AAx");
      bgCol = "#e5772e"; //ezclocker new orange
      marginLeft = "auto";
      textColor = "#ffffff";
    }

    let topStyle = {};
    if (index > 0) {
      //            topStyle.marginTop = '-0.6rem';
    }

    let timeStyle = {
      marginLeft: marginLeft,
      width: "60%",
      paddingTop: "0.2rem",
      paddingBottom: "0.2rem",
      marginTop: "0.4rem",
    };
    let bodyStyle = {
      borderRadius: "6px",
      backgroundColor: bgCol,
      marginLeft: marginLeft,
			marginBottom: "15px",
      width: "60%",
      color: textColor,
			padding: "10px"
    };

    return (
      <>
        <div className="message" style={topStyle}>
          <div
            className="message-header has-text-grey is-size-7"
            style={timeStyle}
          >
            {calcUserNameSpan(msg)} {calcTimeOfDay(msg)}
            {renderDeleteButton(msg)}
          </div>
          <div
            className="message-body has-text-left has-text-weight-semibold is-size-7"
            style={bodyStyle}
          >
            {msg.body}
          </div>
        </div>
      </>
    );
  };
  const renderMessages = () => {
    if (messages == null || messages.length == 0) {
      return <div className="no-message-card">No messages (from {selectedUser.userName})</div>;
    }

    let dividerInfo = {};
    return (
      <>
        <div>
          {messages.map((sheet, index) => (
            <div key={sheet.id} onClick={() => clkOnMsgs()}>
              {renderDateDivider(sheet, dividerInfo)}
              <div className="block">{renderMessage(sheet, index)}</div>
            </div>
          ))}
        </div>
      </>
    );
  };
  const renderDebugInfo = () => {
    if (!debugFlag) return null;

    return (
      <span>
        {msgCounter} {areShowingAllLoadedMsgs ? "y" : "nn"} {messages.length},
        {maxMsgsToShow}
      </span>
    );
  };
  const renderPane = () => {
    if (selectedUser == null) {
      return <div>No user selected</div>;
    }

    return (
      <div>
        <div
          className="card chat-card-wrapper"
          style={{ bottomPadding: "0rem" }}
        >
          {renderDebugInfo()}
          {calcMoreMsgsCouldBeShown() && (
            <div className="btn-loader-box">
              <button
                onClick={clkLoadHistory}
                className="button is-dark is-size-7"
              >
                Load History
              </button>
            </div>
          )}
          <div className="card-content">
            <div className="content">
              {renderMessages()}
              <span className="is-size-7" ref={bottomRef}>
                &nbsp;
              </span>
            </div>
          </div>
          {canSendMessage() && (
            <MessageEditor
              user={selectedUser}
              currentLoggedInUserId={currentLoggedInUserId}
              incMsgCounter={incMsgCounter}
              employer={employer}
              debugFlag={debugFlag}
              allUsers={allUsers}
            />
          )}
          <div>
            <YesNoDialog
              active={active}
              setActive={setActive}
              title={"Delete Message?"}
              messageText={"Click OK to delete this message."}
              doAction={doDeleteMessage}
            />
          </div>
        </div>
      </div>
    );
  };

  return <>{renderPane()}</>;
};
