import React, { useState, useEffect, useRef } from "react";


const MyTimer = ({ intervalMsec, runWhen, tickHandler, visible }) => {
    const [timerInterval, setTimerInterval] = useState(intervalMsec ? intervalMsec : 1000);
    const [timerId, setTimerId] = useState(null);
    const [countX, setCountX] = useState(1);
    const onceFlag = useRef(false);

    useEffect(() => {
        // start:: prevent executing useEffect twice
        if (onceFlag.current) {
            return
        }
        onceFlag.current = true

        if (runWhen && timerId==null) {
            console.log("ue ttttttttttttt");
            const id = setInterval(() => onTick(), timerInterval);
            setTimerId(id);
        } else if (! runWhen) {
            doCleanup();
        }
        return () => doCleanup();
    }, [runWhen, timerId]);

    const doCleanup = () => {
        if (timerId) {
            console.log("mytimerccccccccccccccccccccccccccccccc");
            clearInterval(timerId);
            setTimerId(null);
        }
    }

    // --- clk ----
    const onTick = async () => {
        //console.log('exptick...' + countX);
        //setCountX(countX + 1); //forces re-render
        if (tickHandler) {
            tickHandler(countX);
        }
    }


    // --- render ----
    return (
        <>
            {visible && (
                <span>hey {countX} </span>
            )}
        </>
    );
}

export default MyTimer;
