import React, { useState, useEffect } from "react";
import { sendMessageToAWS } from "../state/MessageSender";
import { sendPushNotificationMessageToEzclocker } from "../state/PushNotificationSender";
import { TO_EVERYONE } from "../state/MsgType";
import { umClearUnreadCount } from "../state/UnreadMsgTracker";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPaperPlane } from "@fortawesome/free-solid-svg-icons";

export const MessageEditor = ({
  user,
  currentLoggedInUserId,
  incMsgCounter,
  employer,
  debugFlag,
  allUsers,
}) => {
  const [value, setValue] = useState("");
  const [otherId, setOtherId] = useState("");

  useEffect(() => {
    setValue("");
  }, [user]);

  //--click--
  const onChange = (ev) => {
    setValue(ev.target.value);
  };
  const handleKey = (ev) => {
    if (ev.key === "Enter") {
      ev.preventDefault();
      onClick();
    }
  };
  const onClick = () => {
    let msgToSend = {
      fromUser: currentLoggedInUserId,
      toUser: user.id,
      body: value,
    };
    sendMsg(msgToSend);
  };
  //simulate sending a msg from otherId to currentLoggedInUserId
  const onClickOther = () => {
    let fromId = otherId.trim();
    if (fromId == "") {
      return;
    }

    let toId = currentLoggedInUserId;
    if (user.id == TO_EVERYONE) {
      console.log("EEEEEEEEEEEEE");
      toId = user.id;
    }

    console.log("simulate send from " + fromId);
    let msgToSend = {
      fromUser: fromId,
      toUser: toId,
      body: value,
    };
    sendMsg(msgToSend);
  };
  const onChangeOtherId = (ev) => {
    setOtherId(ev.target.value);
  };
  //simulate the selected user replying to currentLoggedInUserId
  const onClickToMe = () => {
    let msgToSend = {
      fromUser: user.id,
      toUser: currentLoggedInUserId,
      body: value,
    };
    sendMsg(msgToSend);
  };
  const sendMsg = (msgToSend) => {
    console.log("SEND MSG");
    //console.log(msgToSend);
    sendMessageToAWS(employer.id, msgToSend);
    sendPushNotificationMessageToEzclocker(employer.id, allUsers, msgToSend);
    incMsgCounter();

    setValue("");
    umClearUnreadCount(currentLoggedInUserId, user.id); //clear any unread msgs
  };

  //--render--

  return (
    <>
      <div className="chat-input-box">
        <textarea
          className="textarea textarea-control"
          placeholder={"Type a message:"}
          value={value}
          rows={2}
          onChange={onChange}
          onKeyPress={(e) => handleKey(e)}
        ></textarea>
        <button
          className="button"
          disabled={value.length == 0}
          onClick={onClick}
        >
          <FontAwesomeIcon icon={faPaperPlane} />
        </button>
      </div>
      <div>
        {debugFlag && (
          <>
            <div className="chat-input-box">
              <div>
                <button
                  className="button relative-btn is-info is-outlined"
                  onClick={onClickToMe}
                >
                  To Me
                </button>
              </div>
              <input
                className="input textarea-control"
                type="text"
                placeholder={"some user id"}
                value={otherId}
                onChange={onChangeOtherId}
              ></input>

              <button
                className="button"
                style={{
                  backgroundColor: "#0F4777",
                  color: "#e0e0e0",
                }}
                onClick={onClickOther}
              >
                <FontAwesomeIcon icon={faPaperPlane} />
              </button>
            </div>
          </>
        )}
      </div>
    </>
  );
};
