import React, { useState, useEffect } from "react";
import { sortedlistMessages } from '../graphql/queries';
import { API,graphqlOperation } from 'aws-amplify';
import { appendRawMessages, setCurrentLoggedInUserId,getCurrentChannelID, 
    reCalcUnreadMsgs, getMessageStoreCount } from "../state/MessageStore";
import { formatCDate } from "../util/DateFormatUtil";
import { initReadFlag } from "../state/UnreadMsgTracker";
import { createDatePlusDays } from "../util/DateUtil";

export const MessageStoreComponent = ({currentLoggedInUserId, debugFlag, setIsReady}) => {
    const NUM_LOADS = 5;

    useEffect(() => {
        console.log("loadMessagesStore.. " + currentLoggedInUserId);
        const fetchData = async (cdate, index) => {
            console.log("loadmsgs: " + cdate + ", index: " + index);
            //only want messages for our channel
            const filter = {channelID: {eq: getCurrentChannelID()}};
            //console.log("calling-getMsgsxx");
            const newObj = await API.graphql(graphqlOperation(sortedlistMessages, { cdate: cdate}, filter));
            let ar = newObj.data.sortedlistMessages.items;
            console.log("loadmsgs-back2mz.... " + cdate + ", len: " + ar.length + ", index: " + index);
            //console.log(ar);
            initReadFlag(ar);

            //save into store
            //console.log(currentLoggedInUserId);
            setCurrentLoggedInUserId(currentLoggedInUserId);
            appendRawMessages(ar);
        };

        //be careful here. we issue five async loads, which may not finish in the order we issued them!
        //We fixed this by done each load one at a time
        const fetchAll = async () => {
          let numDays = NUM_LOADS; //last 5 days
          console.log("start fetchAll");
            for(let i = 0; i < numDays; i++) {
              let dt = new Date();
              dt = createDatePlusDays(dt, -numDays + i + 1);
              let cdate = formatCDate(dt);
              let notUsed = await fetchData(cdate, i);
              // console.log("fetchData back " + i)
              if (i == NUM_LOADS - 1) {
                console.log("--- all loads done! ---");
                reCalcUnreadMsgs();     
                setIsReady(true);
              }
            }
        }

        fetchAll();
    // // make sure to catch any error
    // .catch(console.error);
    }, []);


    //--click--
      
  //--render--
  const renderMessages = () => {
    if (!debugFlag) return null;
    return <>
      <div><b>MsgStore: {getMessageStoreCount()} msgs</b>
      </div>
    </>
  }

  return <>
  <div>
    {renderMessages()}
  </div>

  </>;
}


