import React, { useState, useEffect, useRef } from "react";
import MyTimer from "../components/helper/MyTimer";
import { getDirtyFlag, clearDirtyFlag, getStoredViewedUsers } from "./ViewedUserStore";
//import { getUser } from '../graphql/queries';
import { updateUser } from '../graphql/mutations';
import { API,graphqlOperation } from 'aws-amplify';
import { shouldMockViewedUsersToAWS }  from '../environment';
import { getUnreadMsgDirtyFlag, clearUnreadMsgDirtyFlag, buildUserLastMsgIdMap } from "./UnreadMsgTracker";


const ViewedUserSender = ({ currentLoggedInUserId, allUsers }) => {

    // --- clk ----
    const onTick = async () => {
        console.log('tick...');
        if (getDirtyFlag()) {
            console.log('DIRTY!');
            clearDirtyFlag();
            let resp = await sendViewedUserListToAWS();
        }
        if (hasUnreadMsgInfoChanged()) {
            console.log('DIRTY-UNREAD-MSG!');
            clearUnreadMsgDirtyFlag();
            let resp = await sendUnreadMsgMapToAWS();
        }
    }
    const sendViewedUserListToAWS = async () => {
        const result = allUsers.filter((x) => x.id === currentLoggedInUserId);
        if (result.length > 0) { //exists already in store?
            let user = result[0];
            let str = calcIdList();
            console.log("id list: " + str);

            let obj = {
                id: user.id, 
                viewedUsers: str 
            }
            let resp = await doUpdateUser(obj);
            console.log("doup done");
        }
    }
    const hasUnreadMsgInfoChanged = () => {
        if (!getUnreadMsgDirtyFlag()) {
            return false;
        }

        const result = allUsers.filter((x) => x.id === currentLoggedInUserId);
        if (result.length > 0) { //exists already in store?
            let user = result[0];
            let str = buildUserLastMsgIdMap();
            if (user.viewedMsgMap != str) {
                return true;
            }
        }
        console.log('no change');
        return true;
    }

    const sendUnreadMsgMapToAWS = async () => {
        const result = allUsers.filter((x) => x.id === currentLoggedInUserId);
        if (result.length > 0) { //exists already in store?
            let user = result[0];
            let str = buildUserLastMsgIdMap();
            console.log("buildUserLastMsgIdMap: " + str);

            let obj = {
                id: user.id, 
                viewedMsgMap: str 
            }
            let resp = await doUpdateUser(obj);
            console.log("doup done");
        }
    }

    const calcIdList = () => {
        let ar = []; //array of user ids
        for(let i = 0; i < getStoredViewedUsers().length; i++) {
            let user = getStoredViewedUsers()[i];
            if (user.id != "*") {
                ar.push(user.id);
            }
        }

        var stringFromArr = ar.join(',');
        return stringFromArr;
    }


    const doUpdateUser = async (user) => {
        console.log("updateUser " + user.id);
        console.log(user);
        if (shouldMockViewedUsersToAWS()) {
            //do nothing
        } else {
            console.log("updateUser on AWS!");
            const newObj = await API.graphql(graphqlOperation(updateUser, { input: user }));
        }
        console.log("updateUser-back");
    }

    
    // --- render ----
    return (
        <>
        <MyTimer visible={false} intervalMsec="10000" tickHandler={onTick} runWhen={true} />

        </>
    );
}

export default ViewedUserSender;
