import React, { useState, useEffect } from "react";
import { parseEmployerId,parseEmployeeId } from "../../util/CGIParamUtil";
import { getEnvType } from "../../environment";
import { getBaseUrl } from "../../environment";
import { getSortedMsgArray } from "../../state/UnreadMsgTracker"

export const DebugPane = ({appVersion,currentLoggedInUserId, currentLoggedInUserName, isEmployer, employer, employees, allUsers, viewedUsers,setViewedUsers,debugFlag, setDebugFlag}) => {

    useEffect(() => {
    }, []);

    //--click--
    const clkDebugFlag = () => {
        let b = !debugFlag;
        console.log(b + ", " + debugFlag);
        setDebugFlag(b);
    }
   const dumpUsers = () => {
      console.log("---- " + allUsers.length + " users --------");
      for(let i = 0; i < allUsers.length; i++) {
        let user = allUsers[i];
        console.log(user.userName + " " + user.id + " ");
      }
   }
   const dumpMsgs = () => {
    let ar = getSortedMsgArray();
    console.log("---- " + ar.length + " msgs --------");
    console.log(ar);
   }
  
  //--calc--
  const formatBool = (b) => {
    return (b) ? 'true' : 'false';
  }
  const getUserName = () => {
    if (isEmployer) {
        return employer.userName;
    } else {
        let matchAr = employees.filter((x) => x.id === currentLoggedInUserId);
        let userName = (matchAr.length > 0) ? matchAr[0].userName : '?';
        return userName;
    }
  }


  //--render--
  const renderInfo = () => {
    if (! debugFlag) return null;

    let employerId = parseEmployerId();
    let employeeId = parseEmployeeId();

    return <>
    <div>appVersion: {appVersion} </div>
    <div>environment: {getEnvType()} </div>
    <div> baseUrl: {getBaseUrl()} </div>
    <div>employerId (ezclocker): {employerId} , employeeId: {employeeId}, isEmployer: {formatBool(isEmployer)} </div>
    <div>employerId: {employer.id} </div>
    <div>currentLoggedInUserId: <b>{currentLoggedInUserId}</b> name:{currentLoggedInUserName}, nameOnAwS:{getUserName()}</div>
    <div>
      <button onClick={dumpUsers}>Dump Users to console</button>
      <button onClick={dumpMsgs}>Dump Msgs to console</button>
    </div>
    </>
  }

  return <>
    <div>
    <small><button onClick={clkDebugFlag}
                style={{
                  border: "1px solid black",
                  marginLeft : "42px",
                  marginTop: "8px",
                  marginBottom: "10px",
                  borderRadius: "12px",
                  padding: "4px",
                }}>debugPane ({debugFlag ? 'on' : 'off'})</button></small>
    {renderInfo()}
    </div>
  </>;
}


