import React, { useState, useEffect } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faMagnifyingGlass } from "@fortawesome/free-solid-svg-icons";
import { DropdownWithSearch } from "./DropdownWithSearch";
import { sortUserList } from "../../util/UserSorterUtil";

export const SearchDialog = ({ allUsers, viewedUsers, setSelectedUser }) => {
  //    const [active, setActive] = useState(false);
  const [active2, setActive2] = useState(false);
  const [showAllUsers, setShowAllUsers] = useState(true);

  useEffect(() => {
    console.log("SD");
    console.log(viewedUsers);
  }, [viewedUsers]);

  //--click--
  const clkSelectUser = (user) => {
    console.log("sel user" + user.id);
    setSelectedUser(user);
    setActive2(false);
  };
  const checkHandler = () => {
    //do nothing. this is just to avoid react warning message
  };

  //--calc--
  const calcActive2 = () => {
    if (active2) return "modal is-active";
    else return "modal";
  };
  const calcUserStyle = (user) => {
    const baseStyle = {
      backgroundColor: "#0f4777",
      color: "#e0e0e0",
      cursor: "pointer",
      borderBottom: "1px solid #fff",
			borderRadius: "12px",
			textAlign: "center",
    };
    const selectedStyle = {
      backgroundColor: "#18699b",
      color: "#e0e0e0",
    };

    // if (selectedUser && selectedUser.id == user.id) {
    //     return selectedStyle;
    // }
    return baseStyle;
  };
  const calcUserList = () => {
    if (showAllUsers) {
      return allUsers;
    }
    return viewedUsers;
  };

  //--render--
  const renderUsers = () => {
    if (allUsers == null || allUsers.length == 0) {
      return <div>No users</div>;
    }

    console.log(allUsers.length);
    let ar = sortUserList(calcUserList());

    return (
      <>
        {ar.map((user, index) => (
          <div
            key={index}
            className="column"
            style={calcUserStyle(user)}
            onClick={() => clkSelectUser(user)}
          >
            {user.userName}
          </div>
        ))}
      </>
    );
  };

  return (
    <>
      <div className="container p-3">
        <div id="image-modal" className={calcActive2()}>
          <div className="modal-background"></div>
          <div className="modal-content">
            <div className="modal-card">
              <header
                className="modal-card-head p-4"
                style={{ backgroundColor: "hsl(0, 0%, 96%)" }}
              >
                <p className="modal-card-title is-size-6 has-text-weight-bold">Find User</p>
                <button
                  className="delete"
                  onClick={() => setActive2(false)}
                ></button>
              </header>
              <section className="modal-card-body">
                <DropdownWithSearch
                  users={calcUserList()}
                  setSelectedUser={clkSelectUser}
                  isActive={active2}
                />
                <div>
                  <div className="control" style={{ color: "hsl(0, 0%, 7%)" }}>
                    <div className="columns mb-3 mt-3">
                      <div className="column">
                        <small
                          style={{ color: "hsl(0, 0%, 7%)", display: "inline" }}
                        >
                          Or click on a user:
                        </small>
                      </div>
                      <div className="column">
                        <div className="custom-modal-radio-group">
                          <label className="radio">
                            <input
                              type="radio"
                              name="answer"
                              checked={showAllUsers}
                              onChange={checkHandler}
                              onClick={() => setShowAllUsers(true)}
                            />
                            All users
                          </label>
                          <label className="radio">
                            <input
                              type="radio"
                              name="answer"
                              checked={!showAllUsers}
                              onChange={checkHandler}
                              onClick={() => setShowAllUsers(false)}
                            />
                            In your list
                          </label>
                        </div>
                      </div>
                    </div>
                  </div>

                  {renderUsers()}
                </div>
              </section>
            </div>
          </div>
        </div>
        <div>
          <button
            className="button has-text-white is-fullwidth is-rounded find-btn"
            style={{ backgroundColor: "#e5772e" }}
            id="showModal"
            onClick={() => setActive2(true)}
          >
            Find
            <FontAwesomeIcon icon={faMagnifyingGlass} />
          </button>
        </div>
      </div>
    </>
  );
};
