//a very simple data store that can be used between react components
//we store the list of viewed user for the current-logged-in-user

var storedUserIdArray = [];
var dirtyFlag = false; //used to determine when we need to persist the viewlist to AWS

export const setStoredViewedUsers = (viewedUsers) => {
    storedUserIdArray = [...viewedUsers];
    console.log("setStoredViewedUsers");
    console.log(storedUserIdArray);
}

export const addStoredViewedUser = (user) => {
    //storedUserIdArray = [user, ...storedUserIdArray];
    storedUserIdArray.push(user);
    console.log("addStoredViewedUser");
    console.log(storedUserIdArray);
    dirtyFlag = true;
}

export const getStoredViewedUsers = () => {
    return storedUserIdArray;
}
export const getDirtyFlag = () => {
    return dirtyFlag;
}
export const clearDirtyFlag = () => {
    dirtyFlag = false;
}






