import React, { useState, useEffect } from "react";
import { MessagePane } from "./MessagePane";
import { MessageReceiver } from "../state/MessageReceiver";
import { MessageStoreComponent } from "./MessageStoreComponent";
import { UserListPane } from "./UserListPane";
import { SearchDialog } from "./search/SearchDlg";
import {
  addStoredViewedUser,
  getStoredViewedUsers,
} from "../state/ViewedUserStore";

export const ChatContainer = ({
  currentLoggedInUserId,
  isEmployer,
  employer,
  allUsers,
  viewedUsers,
  setViewedUsers,
  debugFlag,
}) => {
  const [msgCounter, setMsgCounter] = useState(0); //used to force rerender when msg sent or arrives
  const [unreadClearedCounter, setUnreadClearedCounter] = useState(0); //used to force rerender when we clear unread msg icon
  const [selectedUser, setSelectedUser] = useState(null);
  const [isReady, setIsReady] = useState(false); //set when we've loaded the messages from aws

  useEffect(() => {
    console.log("init vu");
  }, []);

  //--click--
  const clkAddOrSelectUser = (user) => {
    let matchAr = viewedUsers.filter((x) => x.id === user.id);
    if (matchAr.length == 0) {
      console.log("NOMATCH");
      addStoredViewedUser(user);
      setViewedUsers(getStoredViewedUsers());
    }

    console.log("sel user" + user.id);
    setSelectedUser(user);
  };
  const clkSelectUser = (user) => {
    console.log("sel user" + user.id);
    setSelectedUser(user);
  };
  const incMsgCounter = async () => {
    console.log("i-n-cx " + msgCounter);
    setMsgCounter(msgCounter + 1);
  };

  //--calc--

  //--render--
  const renderEmployerComponent = () => {
    if (employer == null) {
      return <div>No Employerjjj</div>;
    }

    return (
      <>
        <MessageStoreComponent
          currentLoggedInUserId={currentLoggedInUserId}
          debugFlag={debugFlag}
          setIsReady={setIsReady}
        />
      </>
    );
  };
  const renderUsers = () => {
    if (viewedUsers == null || viewedUsers.length == 0) {
      return <div>No users</div>;
    }

    return (
      <>
        <SearchDialog
          allUsers={allUsers}
          viewedUsers={viewedUsers}
          setSelectedUser={clkAddOrSelectUser}
        />

        <UserListPane
          selectedUser={selectedUser}
          setSelectedUser={clkSelectUser}
          currentLoggedInUserId={currentLoggedInUserId}
          viewedUsers={viewedUsers}
          msgCounter={msgCounter}
          isReady={isReady}
          unreadClearedCounter={unreadClearedCounter}
          allUsers={allUsers}
          clkAddOrSelectUser={clkAddOrSelectUser}
        />
      </>
    );
  };

  return (
    <>
      <div
        className="section"
        style={{
          width: "100%",
          paddingTop: "0",
          paddingBottom: "0",
          height: "100%",
          flex: 1,
        }}
      >
        <div>
          <MessageReceiver
            employer={employer}
            incMsgCounter={incMsgCounter}
            debugFlag={debugFlag}
          />
          {renderEmployerComponent()}
        </div>
        <div className="box chat-box-wrapper">
          <div className="columns" style={{margin: 0}}>
            <div
              className="column is-one-quarter"
              style={{
                backgroundColor: "#0f4777",
                color: "#e0e0e0",
                height: "initial",
                overflow: "auto",
                padding: 0,
              }}
            >
              {renderUsers()}
            </div>
            <div className="column" style={{ height: "auto", padding: 0, }}>
              {selectedUser && isReady && (
                <MessagePane
                  selectedUser={selectedUser}
                  currentLoggedInUserId={currentLoggedInUserId}
                  msgCounter={msgCounter}
                  allUsers={allUsers}
                  employer={employer}
                  isEmployer={isEmployer}
                  incMsgCounter={incMsgCounter}
                  debugFlag={debugFlag}
                  unreadClearedCounter={unreadClearedCounter}
                  setUnreadClearedCounter={setUnreadClearedCounter}
                />
              )}
            </div>
          </div>
        </div>
      </div>
    </>
  );
};
