import React, { useState, useEffect } from "react";
import { UnreadMessageIcon } from "./UnreadMessageIcon";
import {
  umClearUnreadCount,
  msgExistsFromUser,
} from "../state/UnreadMsgTracker";
import { sortUserList } from "../util/UserSorterUtil";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCircleUser } from "@fortawesome/free-solid-svg-icons";

export const UserListPane = ({
  selectedUser,
  setSelectedUser,
  currentLoggedInUserId,
  viewedUsers,
  msgCounter,
  isReady,
  unreadClearedCounter,
  allUsers,
  clkAddOrSelectUser,
}) => {
  const [selectCounter, setSelectCounter] = useState(0);

  useEffect(() => {
    console.log("loadUsers..");

    if (isReady && selectedUser == null) {
      //auto-select first user so unread-msg-indicators are displayed for all users in viewedUsers
      console.log("aaaaaaauto-select first user");
      let ar = sortUsers();
      if (ar.length > 0) {
        let user = ar[0];
        //we can't call onClkSelect(usr); because it'll clear the unread msg icon
        console.log("1stseluser.." + user.userName + " id: " + user.id);
        setSelectedUser(user);
        setSelectCounter(selectCounter + 1); //inc
      }
    }
    // // make sure to catch any error
    // .catch(console.error);
  }, [viewedUsers, isReady]);

  useEffect(() => {
    console.log("2nd-use-effectx..");

    if (isReady) {
      for (let i = 0; i < allUsers.length; i++) {
        let user = allUsers[i];
        if (user.id != currentLoggedInUserId) {
          const ar = viewedUsers.filter((x) => x.id == user.id);
          if (ar.length == 0) {
            //use not one of currentLoggedInUserId's viewed users?
            //console.log("not.. " + user.userName);
            if (msgExistsFromUser(currentLoggedInUserId, user.id)) {
              console.log("auto-add user: " + user.id + ", " + user.userName);
              clkAddOrSelectUser(user);
            }
          }
        }
      }
    }
    // // make sure to catch any error
    // .catch(console.error);
  }, [msgCounter]);

  //--click--
  const onClkSelect = (user) => {
    console.log("seluser.." + user.userName + " id: " + user.id);
    //clear unread msg indicator from the currently selected user before we switch to new user
    umClearUnreadCount(currentLoggedInUserId, selectedUser.id);

    setSelectedUser(user);
    umClearUnreadCount(currentLoggedInUserId, user.id);
    setSelectCounter(selectCounter + 1); //inc
  };

  //--calc--
  const calcUserStyle = (user) => {
    const baseStyle = {
      backgroundColor: "#0f4777",
      color: "#e0e0e0",
      // borderBottom: "1px solid #a2bfd2",
    };
    const selectedStyle = {
      backgroundColor: "#18699b",
      color: "#e0e0e0",
      // borderBottom: "1px solid #a2bfd2",
    };

    if (areViewingUser(user)) {
      return selectedStyle;
    }
    return baseStyle;
  };

  const areViewingUser = (user) => {
    if (selectedUser && selectedUser.id == user.id) {
      return true;
    } else {
      return false;
    }
  };

  const calcUserName = (user) => {
    if (user.id == currentLoggedInUserId) {
      return <span className="is-italic is-size-7 flex-shrink-0">(you)</span>;
    }
    return null;
  };
  const calcFullNameWithTooltip = (user) => {
    if (isReady) {
      return (
        <>
          <UnreadMessageIcon
            currentLoggedInUserId={currentLoggedInUserId}
            user={user}
            msgCounter={msgCounter}
            selectCounter={selectCounter}
            areViewingUser={areViewingUser(user)}
            unreadClearedCounter={unreadClearedCounter}
          />
          {user.userName} {calcUserName(user)}
        </>
      );
    }
    return null;
  };
  const sortUsers = () => {
    return sortUserList(viewedUsers);
  };

  //--render--
  const renderViewedUsers = () => {
    if (viewedUsers == null || viewedUsers.length == 0) {
      return <div>No users</div>;
    }

    return (
      <div className="user-card-list">
        {sortUsers().map((user) => (
          <div
            key={user.id}
            className="user-card-box"
            style={calcUserStyle(user)}
            onClick={() => onClkSelect(user)}
          >
            <FontAwesomeIcon icon={faCircleUser} className="user-icon" />
            {calcFullNameWithTooltip(user)}
          </div>
        ))}
      </div>
    );
  };

  return <>{renderViewedUsers()}</>;
};
