import React, { useState, useEffect, useRef } from "react";
import { listUsers } from '../graphql/queries';
import { API,graphqlOperation } from 'aws-amplify';
import { setStoredViewedUsers } from "../state/ViewedUserStore";
import { shouldMockServerCalls } from '../environment';
import { TO_EVERYONE } from "../state/MsgType";
import { initUserLastMsgIdMap } from '../state/UnreadMsgTracker';


export const SecondStartupComponent = ({employer, isEmployer, loggedInEmployeeId, setAllUsers, setViewedUsers, setIsReady, setCurrentLoggedInUserId, debugFlag}) => {
    const [commError, setCommError] = useState(false);
    const onceFlag = useRef(false);

    let mockFlag = shouldMockServerCalls(); //when we run the react app locally you need to set this to true

    useEffect(() => {
        //console.log("init StartupComponent");
        // declare the data fetching function
        const fetchData = async (cdate) => {
            try {
                // start:: prevent executing useEffect twice
                if (onceFlag.current) {
                    return
                }
                onceFlag.current = true
                // end:: prevent executing useEffect twice

                console.log("4. get users");
                let employerGuid = employer.id;
                const resp4 = await getAWSUsers(employerGuid);

                //TODO do sync here asynchrously

                console.log("X. done");
                setIsReady(true);
            } catch (error) {
                console.log("COMM-ERRRRRRRRRRRR");
                console.log(error);
                setCommError(true);
           }
        };

        fetchData();
        //console.log("StartupComponent init done");
    }, []);


    //-- api ---
    const getAWSUsers = async (employerId) => {
        console.log("calling-listUsers " + employerId + ", " + mockFlag);
        const params = {
            limit: 200, //TODO is there a max???
            sortDirection: 'DESC'      
          };       
          const filter = {employerID: {eq: employerId}};
   
        const newObj = await API.graphql(graphqlOperation(listUsers, { filter:filter, input: params }));
        console.log("back-listUsers ");
        let ar = newObj.data.listUsers.items;

        if (mockFlag) {
            //to test scrolling set k to 10 or 20
            for(let k = 0; k < 0; k++) {
                let extraUser = {
                    employerID: employerId,
                    id: `FFAA77-${k}`,
                    userName: `Extra-${k}`,
                    createdAt: "2023-10-18T15:02:22.570Z",
                    updatedAt: "2023-10-18T15:02:22.570Z"
                };
                console.log(extraUser);
                ar.push(extraUser);
            }
        }
        console.log(ar);
        setAllUsers(ar);
        let currentUser = doSetCurrentLoggedInUserId(ar);

        //create the Everyone channel
        let everyoneUser = {
            employeeID: employerId,
            id: TO_EVERYONE,
            userName: "Team",
            createdAt: "2023-10-18T15:02:22.570Z",
            updatedAt: "2023-10-18T15:02:22.570Z"
        };
        let firstUser = everyoneUser;
        let secondUser = null;
        if (!isEmployer) {
            console.log("are employee");
            secondUser = findEmployerUserId(ar);
        }
//        let ar2 = [everyoneUser, ...ar]; //everyone should be first

        let ar2 = buildViewedUserList(currentUser, ar, firstUser, secondUser, isEmployer);
        setViewedUsers(ar2); //later this can be a subset of allusers
        setStoredViewedUsers(ar2); //save for timer that updates user.viewedUsers in AWS
        initUserLastMsgIdMap(currentUser);
    }

    const buildViewedUserList = (currentUser, userList, firstUser, secondUser, isEmployer) => {
        console.log("buildViewedUserList");
        let ar2 = [];
        if (firstUser) {
            ar2.push(firstUser);
        }
        if (secondUser) {
            ar2.push(secondUser);
        }

        if (currentUser && currentUser.viewedUsers && currentUser.viewedUsers.length > 0) {
            let idAr = currentUser.viewedUsers.split(',');
            console.log(currentUser.viewedUsers);
            for(let i = 0; i < idAr.length; i++) {
                let id = idAr[i];
                console.log(" .. " + id);
                const result = userList.filter((x) => x.id === id);
                if (result.length > 0) { //exists already in store?
                    let user = result[0];
                    //don't add firstUser or secondUser again
                    if (isUserMatch(firstUser, user) || isUserMatch(secondUser, user)) {
                    } else {
                        if (currentUser && user.id != currentUser.id) {
                            ar2.push(user);
                        }
                    }
                }
            }
        }

        //for employers, automatically view all users
        if (isEmployer) {
            ar2 = copyAllRemainingEmployees(ar2, userList);
        }

        //ensure current user 'you' is not in list
        if (currentUser) {
            ar2 = ar2.filter((x) => x.id !== currentUser.id);
        }

        console.log("vl len " + ar2.length);
        return ar2;
    }
    const isUserMatch = (firstUser, user) => {
        if (firstUser && user.id == firstUser.id) {
            return true;
        }
        return false;
    }
    const doSetCurrentLoggedInUserId = (userList) => {
        for(let i = 0; i < userList.length; i++) {
            let user = userList[i];
            if (loggedInEmployeeId) {
                if (user.employeeID == loggedInEmployeeId) {
                    console.log("doSetCurrentLoggedInUserId: " + user.id);
                    setCurrentLoggedInUserId(user.id);
                    return user;
                }
            } else if (user.employeeID == null) {
                console.log("doSetCurrentLoggedInUserId (employer): " + user.id);
                setCurrentLoggedInUserId(user.id);
                return user;
            }
        }
        return null;
    }
    const findEmployerUserId = (userList) => {
        for(let i = 0; i < userList.length; i++) {
            let user = userList[i];
            if (user.employeeID == null) {
                return user;
            }
        }
        return null;
    }
    const copyAllRemainingEmployees = (currentAr, userList) => {
        let ar2 = [...currentAr]; //start with contents of currentAr
        console.log("DD " + ar2.length);

        for(let i = 0; i < userList.length; i++) {
            let user = userList[i];
            let matchAr = currentAr.filter((x) => x.id === user.id);
            if (matchAr.length == 0) {
                ar2.push(user);
            }
        }
        console.log("DD " + ar2.length);
        return ar2;
    }

    //--click--

    //--calc--


    //--render--
    const renderCommError = () => {
        if (commError) { return <>
          COMM-ERROR!
        </>}
        return null;
    }
    return <>
    {debugFlag && (
        <div>
            error: {renderCommError()}
        </div>
    )}
    </>;
}


