import React, { useState, useEffect, useRef } from "react";
import { doEzClockerAPIGET } from '../api/ServerAPI';
import { getEmployer, listEmployees } from '../graphql/queries';
import { API, graphqlOperation } from 'aws-amplify';
import { shouldMockServerCalls } from '../environment';
import { parseEmployerId, parseEmployeeId } from "../util/CGIParamUtil";
import { createEmployer, createUser } from '../graphql/mutations';
import awsExports from '../aws-exports';
import { setPushNotificationEnabled } from "../state/PushNotificationSender";

export const StartupComponent = ({ initAmplify, setIsEmployer, setLoggedInEmployeeId, setCurrentLoggedInUserName,
    setEmployer, setEmployees, setIsReadyForSync, debugFlag }) => {
    const [commError, setCommError] = useState(false);
    const onceFlag = useRef(false);

    let mockFlag = shouldMockServerCalls(); //when we run the react app locally you need to set this to true

    useEffect(() => {
        //console.log("init StartupComponent");
        // declare the data fetching function
        const fetchData = async (cdate) => {
            try {
                // start:: prevent executing useEffect twice
                if (onceFlag.current) {
                    return
                }
                onceFlag.current = true
                // end:: prevent executing useEffect twice

                console.log("1. get guids");
                const employerInfo = await getEzClockerInfo();
                const employerGuid = employerInfo.guid;

                initAmplify(employerInfo.awsApiKey, employerInfo.useProductionAWSEnv);

                console.log("2. get employer");
                const resp2 = await findOrCreateAWSEmployer(employerGuid, employerInfo);

                //don't need these unless are doing sync
                console.log("3. get employees");
                const resp3 = await getAWSEmployees(employerGuid);

                console.log("X. done (first startup)");
                setIsReadyForSync(true);
            } catch (error) {
                console.log("COMM-ERRRRRRRRRRRR");
                console.log(error);
                setCommError(true);
            }
        };

        fetchData();
        //console.log("StartupComponent init done");
    }, []);


    //-- api ---
    const getEzClockerInfo = async () => {
        if (mockFlag) {
            console.log("MOCKcall to ezclocker!!")
            let guid = "75b68615-9124-468d-ab55-eae6f12db545";
            setIsEmployer(true);

            let xurl = buildUrl();
            console.log(xurl);
            if (xurl.includes("employeeId=")) {
                console.log("EMPLOYEE login!!");
                setIsEmployer(false);
                setLoggedInEmployeeId("213409c0-35d5-4380-8b5a-d5e99cc2baf8");
                setCurrentLoggedInUserName("Emp1");
            } else {
                setCurrentLoggedInUserName("Boss");
            }
            setPushNotificationEnabled(true); 

            let resp = {
                guid: guid,
                userName: 'SomeEmployer',
                email: null,
                awsApiKey: awsExports.aws_appsync_apiKey,
                useProductionAWSEnv: false
            }
            return resp;
        }

        console.log("getEzClockerInfo..");
        let url = buildUrl();
        const json = await doEzClockerAPIGET(url);

        if (json) {
            //console.log("Okkkkkkkkkkkkkk");
            //console.log(json);
            json.employerInfo.awsApiKey = json.awsApiKey;
            json.employerInfo.useProductionAWSEnv = json.useProductionAWSEnv;
            setPushNotificationEnabled(json.pushNotificationEnabled);

            if (json.employeeInfo) {
                setIsEmployer(false);
                setLoggedInEmployeeId(json.employeeInfo.guid);
                setCurrentLoggedInUserName(json.employeeInfo.userName);
                return json.employerInfo;
            } else if (json.employerInfo) {
                setIsEmployer(true);
                setLoggedInEmployeeId(null);
                setCurrentLoggedInUserName(json.employerInfo.userName);
                return json.employerInfo;
            }
        }
        return null;
    }

    const buildUrl = () => {
        let employerId = parseEmployerId();
        let employeeId = parseEmployeeId();

        if (employeeId == null) {
            let url = `/teamchat/info?employerId=${employerId}`;
            return url;
        } else {
            let url = `/teamchat/info?employerId=${employerId}&employeeId=${employeeId}`;
            return url;
        }
    }

    const findOrCreateAWSEmployer = async (employerId, employerInfo) => {
        let resp = await getAWSEmployer(employerId);
        if (resp == null) {
            //the first time chat is launched for an employer, we'll need to create employer in aws
            resp = await doCreateEmployer(employerId, employerInfo);
            let resp2 = await doCreateUser(employerInfo);
        }
        return resp;
    }

    const getAWSEmployer = async (employerId) => {
        console.log("calling-getEmployer " + employerId);
        const newObj = await API.graphql(graphqlOperation(getEmployer, { id: employerId }));
        console.log("back-getEmployer.............");
        let obj = newObj.data.getEmployer;
        console.log(obj);
        if (obj) {
            setEmployer(obj);
        }
        return obj;
    }
    const getAWSEmployees = async (employerId) => {
        console.log("calling-getEmployees " + employerId);
        const params = {
            limit: 200, //TODO is there a max???
            sortDirection: 'DESC'
        };
        const filter = { employerID: { eq: employerId } };
        // listEmployees(filter: {employerID: {eq: "75b68615-9124-468d-ab55-eae6f12db545"}}) {

        const newObj = await API.graphql(graphqlOperation(listEmployees, { filter: filter, input: params }));
        console.log("back-listEmployees");
        let ar = newObj.data.listEmployees.items;
        console.log(ar);
        setEmployees(ar);
    }

    const doCreateEmployer = async (employerId, employerInfo) => {
        console.log("createEmployer " + employerId);
        let domain = window.location.hostname;
        console.log("domain: " + domain);
        let email = employerInfo.email ? employerInfo.email : 'placeholder@dummy.com';
        let obj = {
            id: employerId,
            domain: domain,
            userName: employerInfo.userName,
            email: email
        };

        const newObj = await API.graphql(graphqlOperation(createEmployer, { input: obj }));
        console.log("createEmployer-back");
        setEmployer(obj);
    }
    const doCreateUser = async (empInfo) => {
        console.log("createUser(employer) " + empInfo.userName);
        let obj = {
            userName: empInfo.userName,
            employerID: empInfo.guid, //the employer's own id
            employeeID: null,
            viewedUsers: null,
            viewedMsgMap: null         
        };

        const newObj = await API.graphql(graphqlOperation(createUser, { input: obj }));
        console.log("createUser-back");
    }

    //--click--

    //--calc--


    //--render--
    const renderCommError = () => {
        if (commError) {
            return <>
                COMM-ERROR!
            </>
        }
        return null;
    }
    return <>
        {debugFlag && (
            <div>
                <div>
                    error: {renderCommError()}
                </div>
            </div>
        )}
    </>;
}


