//sends the message to EZClocker which can do the push notification to the user's phone
import { shouldMockServerCalls } from "../environment";
import { doEzClockerAPIPOST } from "../api/ServerAPI";
import { TO_EVERYONE } from "./MsgType";

var pushNotificationEnabled = true; //whether the current employer has push notifications enabled

export const setPushNotificationEnabled = (flag) => {
    pushNotificationEnabled = flag;
    console.log("pushNotificationEnabled: " + pushNotificationEnabled);
}


export const sendPushNotificationMessageToEzclocker = async (employerId, allUsers, msg) => {
    if (! pushNotificationEnabled) {
        return;
    }

    console.log("sendPushNotificationMessageToEzclocker ====> ");
    let mockFlag = shouldMockServerCalls(); //when we run the react app locally you need to set this to true

    let obj = {
        fromGuid: findEmployerOrEmployeeGuid(msg.fromUser, allUsers),
        toGuid: findEmployerOrEmployeeGuid(msg.toUser, allUsers),
        body: msg.body,
        channelID: msg.channelID
    }
    console.log(obj);

    if (mockFlag) {
    } else {
        let url = `/teamchat/pushNotify?employerId=${employerId}`;
        const json = await doEzClockerAPIPOST(url, obj);
        console.log("backFromEzclocker");
    }
}

const findEmployerOrEmployeeGuid = (userId, allUsers) => {
    if (userId == TO_EVERYONE) {
        return TO_EVERYONE;
    }
    let ar = allUsers.filter((x) => x.id === userId);
    if (ar.length > 0) {
        let user = ar[0];
        let guid = user.employeeID ? user.employeeID : user.employerID;
        return guid;
    }
    return null;
}
