//track unread messages for each user
//Jan30/2024: This is a new implemention and replaces the old UnreadMessageTrackerer.js
import { TO_EVERYONE } from "./MsgType";

//we keep a sorted list of the msgs in the msg store 
var sortedMsgArray = [];
const userLastMsgIdMap = new Map(); //userId, lastMsgId
var dirtyFlag = false; //used to determine when we need to persist the viewlist to AWS


export const setListForUnreadMsgCalc = (msgList) => {
    if (msgList) {
        sortedMsgArray = sortByDateDesc(msgList);
        console.log("UM set ", sortedMsgArray.length);
    }
}
export const getSortedMsgArray = () => {
    return sortedMsgArray;
}

export const getNumUnreadMsgs = (currentLoggedInUserId, userId) => {
    let ar = getNumUnreadMsgsEx(currentLoggedInUserId, userId);
    //ar considers msgs both to and from userId, so we need to filter a bit more here
    // console.log("BLONDE" + ar.length);
    let count = 0;
    for(let i = 0; i < ar.length; i++) {
        let msg = ar[i];
        let processMsg = false;
        //console.log(msg);
        if (userId == TO_EVERYONE) {
            if (msg.fromUser != currentLoggedInUserId) { //employer shouldn't count the msgs they sent
                processMsg = true;
            }
        } else {
            if (msg.fromUser == userId) { //because currentLoggedInUserId has read the msgs they sent!
                processMsg = true;
            }
        }
    
        if (processMsg && ! msg.haveRead) {
            count++;
        }
    }
    return count;
}
const getNumUnreadMsgsEx = (currentLoggedInUserId, userId) => {
    let tmp = getMsgsSinceLastRead(userId);
    //console.log(userId + ", len " + tmp.length);
    //console.log(sortedMsgArray);
    const msgsSinceLastRead = tmp.filter((x) => !x.haveRead);

    let ar = [];
    if (userId == TO_EVERYONE) {
        ar = findMessagesForUserInGroupConversationEx(msgsSinceLastRead, userId);
    } else {
        ar = findMessagesForUserEx(msgsSinceLastRead, currentLoggedInUserId, userId);
    }
    console.log("cvvv " + msgsSinceLastRead.length + " vvv " + ar.length + " vv " + userId);
    return ar;
}

//when a user is clicked on, we show the msgs between the loggedInUser and the selected user
//set all these msgs as read.
export const setReadFlag = (userId, viewedList) => {
    let lastMsgId = null;
    for(let i = 0; i < viewedList.length; i++) {
        let msg = viewedList[i];
        msg.haveRead = true;
        lastMsgId = msg.id;
    }
    if (lastMsgId) {
        userLastMsgIdMap.set(userId, lastMsgId);
        dirtyFlag = true;
    }
}
export const umClearUnreadCount = (currentLoggedInUserId, userId) => {
    let ar = getNumUnreadMsgsEx(currentLoggedInUserId, userId);
    console.log("umclr " + ar.length);
    setReadFlag(userId, ar);
}

//init msg.haveRead = false
export const initReadFlag = (ar) => {
    for(let i = 0; i < ar.length; i++) {
        let msg = ar[i];
        msg.haveRead = false;
    }
}

//true if any msgs from user and to currentLoggedInUserId
export const msgExistsFromUser = (currentLoggedInUserId, userId) => {
    let ar = sortedMsgArray.filter((x) => x.fromUser == userId);
    ar = ar.filter((x) => x.toUser == currentLoggedInUserId);
    if (ar.length > 0) {
        return true;
    }
    return false;
}

//--userMsgMap --
export const initUserLastMsgIdMap = (currentUser) => {
    if (currentUser) {
        if (currentUser.viewedMsgMap) {
            console.log("viewedMsgMap:" + currentUser.viewedMsgMap);
            const ar = currentUser.viewedMsgMap.split(",");
            for (let k = 0; k < ar.length; k++) {
                let str = ar[k];
                const inner = str.split("~");
                if (inner.length == 2) {
                    const u = inner[0];
                    const msgid = inner[1];
                    userLastMsgIdMap.set(u, msgid);
                }
            }
        }
    }
}
export const buildUserLastMsgIdMap = () => {
    let str = '';
    let isFirst = true;
    for (const [key, value] of userLastMsgIdMap) { 
       //console.log(`The value for key ${key} is ${value}`);
       if (!isFirst) {
        str += ",";
       }
       str += key + "~" + value;
       isFirst = false;
    }
    return str;
}
export const getUnreadMsgDirtyFlag = () => {
    return dirtyFlag;
}
export const clearUnreadMsgDirtyFlag = () => {
    dirtyFlag = false;
}


// -- helpers --
const sortByDateDesc = (msgs) => {
    let ar = msgs.sort((a, b) => {
        if (a.createdAt < b.createdAt) {
            return -1;
        }
        if (a.createdAt > b.createdAt) {
            return 1;
        }
        return 0;
    });
    return ar;
}

const getMsgsSinceLastRead = (userId) => {
    if (! userLastMsgIdMap.has(userId)) {
        return sortedMsgArray;
    } else {
        let targetId = userLastMsgIdMap.get(userId);

        let tmpL = [];
        let haveSeen = false;
        let veryOldL = [];
        for(let i = 0; i < sortedMsgArray.length; i++) {
            let msg = sortedMsgArray[i];
            if (!haveSeen) {
                if (msg.id == targetId) {
                    haveSeen = true;
                }
                //sortedMsgArray only contains the last few days of msgs. So targetId may not be in the array
                veryOldL.push(msg);
            } else {
                tmpL.push(msg); //all msgs after targetId
            }
        }

        if (! haveSeen) {
            tmpL = veryOldL;
        }

        return tmpL;
    }
}

//copy of fns from msg store. finds both msgs currentLoggedInUserId->userId and userId->currentLoggedInUserId
export const findMessagesForUserEx = (ar, currentLoggedInUserId, userId) => {
    let newArray = [];
    for(let i = 0; i < ar.length; i++) {
        let msg = ar[i];
        if (msg.fromUser == currentLoggedInUserId && msg.toUser == userId) { //A -> B
            newArray.push(msg); //use new msg
        } else if (msg.fromUser == userId && msg.toUser == currentLoggedInUserId) { //B -> A
            newArray.push(msg); //use new msg
        }
    }

    let result = newArray;
    return result;
}

//here userId is '*'. finds msgs where toUser is *
export const findMessagesForUserInGroupConversationEx = (ar, userId) => {
    let newArray = [];
    for(let i = 0; i < ar.length; i++) {
        let msg = ar[i];
        if (msg.toUser == userId) { //A -> B
            newArray.push(msg); //use new msg
        }
    }

    let result = newArray;
    return result;
}
    



