import React, { useState, useEffect, useRef } from "react";

export const DropdownWithSearch = ({ users, setSelectedUser, isActive }) => {
  const [userName, setUserName] = React.useState("");
  const [dropDownActive, setDropDownActive] = React.useState(false);
  let ref = useRef();

  useEffect(() => {
    setUserName("");
  }, [isActive]);

  useEffect(() => {
    const handler = (event) => {
      if (
        dropDownActive &&
        ref.current &&
        !ref.current.contains(event.target)
      ) {
        setDropDownActive(false);
      }
    };
    document.addEventListener("mousedown", handler);
    document.addEventListener("touchstart", handler);
    return () => {
      // Cleanup the event listener
      document.removeEventListener("mousedown", handler);
      document.removeEventListener("touchstart", handler);
    };
  }, [dropDownActive]);

  // --- click ---
  const onClickItem = (e, emp) => {
    e.preventDefault();
    console.log("AAAAAAAAAA " + emp.userName);
    setDropDownActive(false);
    setSelectedUser(emp);
    setUserName("");
  };

  //--calc --
  const calcDropdownClass = () => {
    if (dropDownActive) {
      return "dropdown is-active";
    } else {
      return "dropdown;";
    }
  };
  const calcDropdownMenuClass = () => {
    if (dropDownActive) {
      return "dropdown-menu";
    } else {
      return "dropdown-menu is-hidden;";
    }
  };
  //--render --
  //
  const renderDropdownContent = () => {
    //console.log(userName);
    if (userName == null || userName == "") return null;

    let ar = [];

    let target = userName.toLowerCase();
    for (let i = 0; i < users.length; i++) {
      let emp = users[i];
      if (emp.userName.toLowerCase().includes(target)) {
        ar.push(emp);
      }
    }

    // let myClass = {
    //     backgroundColor: isHover ? 'lightblue' : 'rgb(0, 191, 255)',
    //     color: isHover ? 'red' : 'green'
    // };

    return (
      <>
        {ar.map((row) => (
          <a
            key={row.id}
            href="#"
            className="dropdown-item"
            onClick={(e) => onClickItem(e, row)}
          >
            {row.userName}
          </a>
        ))}
      </>
    );
  };

  return (
    <>
      <div className={calcDropdownClass()}>
        <div className="dropdown-trigger">
          <div className="columns is-align-items-center">
            <div className="column is-one-quarter">
              <small style={{ color: "hsl(0, 0%, 7%)" }}>
                Type a user name:
              </small>
            </div>
            <div className="column">
              <input
                aria-haspopup="true"
                aria-controls="dropdown-menu"
                className="input is-medium"
                id="uname"
                label="User Name"
                value={userName}
                onChange={(event) => {
                  setUserName(event.target.value);
                  setDropDownActive(true);
                }}
              />
            </div>
          </div>
        </div>
        <div className={calcDropdownMenuClass()} id="dropdown-menu" role="menu">
          <div className="dropdown-content" ref={ref}>
            {renderDropdownContent()}
          </div>
        </div>
      </div>
    </>
  );
};
