import logo from './logo.svg';
import './App.css';
import { Amplify } from 'aws-amplify';
import awsExports from './aws-exports';
import awsExportsProduction from './aws-exports-production';
import 'bulma/css/bulma.min.css';
import { ChatContainer } from './components/ChatContainer';
import { StartupComponent } from './components/StartupComponent';
import { SecondStartupComponent } from './components/SecondStartupComponent';
import { EzClockerSyncComponent } from './components/EzClockerSyncComponent';
import React, { useState, useEffect } from "react";
import { DebugPane } from './components/debug/DebugPane';
import ViewedUserSender from './state/ViewedUserSender';
import { ImpersonateUserSelector } from './components/debug/ImpersonateUserSelector'
import { getEnvType } from './environment';
import { setCurrentChannelID } from "./state/MessageStore";
import  VERSION from './Version';

console.log('------------------------EZTEAMCHAT: (env:' + getEnvType() + ')-----------------------');
console.log(getEnvType());
//console.log(awsExports.aws_appsync_graphqlEndpoint);
//console.log(awsExports.aws_appsync_apiKey);

//Amplify.configure(awsExports);

function App() {
  const [currentLoggedInUserId, setCurrentLoggedInUserId] = useState(null); 
  const [currentLoggedInUserName, setCurrentLoggedInUserName] = useState(null); 
  const [isEmployer, setIsEmployer] = useState(false); 
  const [loggedInEmployeeId, setLoggedInEmployeeId] = useState(null); //if isEmployer is false then this holds employee guid
  const [employer, setEmployer] = useState(null);
  const [employees, setEmployees] = useState([]);
  const [allUsers, setAllUsers] = useState(null);
  const [viewedUsers, setViewedUsers] = useState(null);
  const [isReadyForSync,setIsReadyForSync] = useState(false);
  const [isReadyForSecondStartup,setIsReadyForSecondStartup] = useState(false);
  const [isReady, setIsReady] = useState(false); 
  const [debugFlag, setDebugFlag] = useState(false);
  const [impersonatedUser, setImpersonatedUser] = useState(null);

  //--click--
  const initAmplify = (apiKey, useProductionAWSEnv) => {
    //console.log("API key from ez " + apiKey);
    console.log("prod-flag: " + useProductionAWSEnv);
    awsExports.aws_appsync_apiKey = apiKey;
    if (useProductionAWSEnv == true) {
      awsExports.aws_appsync_graphqlEndpoint = awsExportsProduction.aws_appsync_graphqlEndpoint;
    }
    //console.log(awsExports.aws_appsync_apiKey);
    Amplify.configure(awsExports);
    console.log("Amplify.config done.")
  }
  const setTheEmployer = (employerParam) => {
    setEmployer(employerParam);
    setCurrentChannelID(employerParam.id);
  }

  //--calc--

  //--render--
  const render = () => {
      return <> 
        <StartupComponent 
          initAmplify={initAmplify}
          setIsEmployer={setIsEmployer} 
          setLoggedInEmployeeId={setLoggedInEmployeeId}
          setCurrentLoggedInUserName={setCurrentLoggedInUserName}
          setEmployer={setTheEmployer}
          setEmployees={setEmployees}
          setIsReadyForSync={setIsReadyForSync}
          debugFlag={debugFlag}
          />

        {isReadyForSync && (
          <EzClockerSyncComponent 
            employer={employer}
            employees={employees}
            isEmployer={isEmployer}
            debugFlag={debugFlag}
            setIsReady={setIsReadyForSecondStartup}
            />
        )}

        {isReadyForSecondStartup && (
          <SecondStartupComponent 
            employer={employer} 
            isEmployer={isEmployer}
            loggedInEmployeeId={loggedInEmployeeId}
            setAllUsers={setAllUsers}
            setViewedUsers={setViewedUsers}
            setCurrentLoggedInUserId={setCurrentLoggedInUserId} 
            debugFlag={debugFlag}
            setIsReady={setIsReady}
            />
        )}

        {isReady && (
          <ChatContainer 
            currentLoggedInUserId={currentLoggedInUserId} 
            isEmployer={isEmployer} 
            employer={employer}
            employees={employees}
            allUsers={allUsers}
            viewedUsers={viewedUsers}
            setViewedUsers={setViewedUsers}
            debugFlag={debugFlag}
            />
        )}

        {isReady && (
            <ViewedUserSender 
            currentLoggedInUserId={currentLoggedInUserId}             
            allUsers={allUsers}
          />
        )}
      </>
  }

  return (
    <div className="container main-wrapper">
      <div className="main-wrapper-inner-box">
        {render()}
        <DebugPane
            appVersion={VERSION}
            currentLoggedInUserId={currentLoggedInUserId} 
            currentLoggedInUserName={currentLoggedInUserName} 
            isEmployer={isEmployer} 
            employer={employer}
            employees={employees}
            allUsers={allUsers}
            viewedUsers={viewedUsers}
            setViewedUsers={setViewedUsers}
            debugFlag={debugFlag}
            setDebugFlag={setDebugFlag}
            />

        {(isReady && debugFlag) && (
              <ImpersonateUserSelector             
              currentLoggedInUserId={currentLoggedInUserId} 
              setCurrentLoggedInUserId={setCurrentLoggedInUserId} 
              allUsers={allUsers}
              impersonatedUser={impersonatedUser}
              setImpersonatedUser={setImpersonatedUser}
              />
          )}

      </div>
    </div>
  );
}

export default App;
