//load older messages
import { adjustDateByMonth,formatCDate, convertCDateToDate } from "../util/DateFormatUtil";
import { listMessages } from '../graphql/queries';
import { API,graphqlOperation } from 'aws-amplify';
import { appendRawMessagesAndReCalc } from "./MessageStore";
import { initReadFlag } from "./UnreadMsgTracker";

var sessionInfoArray = []; //array of { id:userId, loadedCDateArray }

export const haveReachedMaxHistory = (userId) => {
    let info = findSessionInfo(userId);
    let n = info.loadedCDateArray.length;
    //console.log("n: " + n);
    return n > 4; //at most one year
}
export const calcMonthToLoad = (userId) => {
    let info = findSessionInfo(userId);
    let cdate = null;
    let n = info.loadedCDateArray.length;
    if (n == 0) {
        cdate = formatCDate(new Date());
    } else {
        cdate = info.loadedCDateArray[n - 1]; 
        let dtInitial = convertCDateToDate(cdate);
        console.log(dtInitial);
        let previosMonthDt = adjustDateByMonth(dtInitial, -1);
        console.log(previosMonthDt);
        cdate = formatCDate(previosMonthDt);
    }

    console.log("final " + cdate);
    return cdate;
}
export const loadMessagesForUserAndMonth = async (userId, cdate) => {
    console.log("loadMessagesForUserAndMonth: " + userId + ", " + cdate);
    let info = findSessionInfo(userId);
    info.loadedCDateArray.push(cdate); //always add even if no msgs found

    // listMessages(filter: {cdate: {beginsWith: "202311"}, and: {toUser: {eq: "02167728-1dd2-4c92-94a2-86d0c859735e"}}}) {
    let monthCDate = cdate.substring(0,6);
    console.log("calling-listMessages " + monthCDate);
    const params = {
        limit: 200, //TODO is there a max???
        sortDirection: 'DESC'      
      };       
    const filter = {toUser: {eq: userId},
                    and: {cdate: {beginsWith: monthCDate}}
            };

    const newObj = await API.graphql(graphqlOperation(listMessages, { filter:filter, input: params }));
    console.log("back-listMessages");
    let ar = newObj.data.listMessages.items;
    console.log(ar);
    initReadFlag(ar);
    appendRawMessagesAndReCalc(ar);
    return 0;
}


export const calcOldestCDate = (messages) => {
    if (messages.length == 0) return null;
    let el1 = messages[0].cdate;
    return el1; //1st msg in array is oldest
}


const findSessionInfo = (userId) => {
    for (let i = 0; i < sessionInfoArray.length; i++) {
        let info = sessionInfoArray[i];
        if (info.id == userId) {
            return info;
        }
    }

    //auto-crete one
    console.log("MHAuto-create sessioninfo.........");
    let newInfo = {
        id: userId,
        loadedCDateArray: []
    }
    sessionInfoArray.push(newInfo);
    return newInfo;
}
