//calling ezclocker
import { getBaseUrl } from "../environment";

const baseUrl = getBaseUrl(); 
const apiUrl = '/_api/v1';


// ------------------ GET ------------------------------------
export const doEzClockerAPIGET = async (urlParam) => {
    let url = apiUrl +  urlParam;
    return doHttpGET(url);
}
export const doHttpGET = async (urlParam) => {
    let url = buildUrl(urlParam);
    console.log("GET " + url);
    let hdr = buildHeader('GET');
    const response = await fetch(url, hdr);
    console.log("BackFromGET");
    const json = await response.json();
    return json;
}

// ------------------ POST ------------------------------------
export const doEzClockerAPIPOST = async (urlParam, body) => {
    let url = apiUrl +  urlParam;
    return doHttpPOST(url, body);
}
export const doHttpPOST = async (urlParam, body) => {
    let url = buildUrl(urlParam);
    console.log("POST " + url);
    let hdr = buildHeader('POST');
    hdr.body = JSON.stringify(body); // body data type must match "Content-Type" header

    const response = await fetch(url, hdr);
    console.log("BackFromPOST");
    const json = await response.json();
    return json;
}


const buildUrl = (urlParam) => {
    let url = baseUrl + urlParam;
    return url;
}

const buildHeader = (method) => {
    let header = {
        method: method,
        //mode: "cors", // no-cors, *cors, same-origin
        cache: "no-cache", // *default, no-cache, reload, force-cache, only-if-cached
        credentials: "same-origin", // include, *same-origin, omit
        headers: {
            "Content-Type": "application/json",
        },
        redirect: "follow", // manual, *follow, error
        referrerPolicy: "no-referrer", // no-referrer, *no-referrer-when-downgrade, origin, origin-when-cross-origin, same-origin, strict-origin, strict-origin-when-cross-origin, unsafe-url
    };
    return header;
}


export default {
    doHttpGET
};
