import React, { useState, useEffect } from "react";
import { getNumUnreadMsgs } from "../state/UnreadMsgTracker";

export const UnreadMessageIcon = ({currentLoggedInUserId, user, msgCounter, selectCounter, areViewingUser, unreadClearedCounter}) => {
    const [unreadCount, setUnreadCount] = useState(0);

    useEffect(() => {
        calcAndSetUnreadCount();
    }, [user, msgCounter, selectCounter, unreadClearedCounter]);

    //--click--
    // const onClickClearCount = () => {
    //     let ar = calcAndSetUnreadCount();
    //     clearUnreadMessages(user, ar, currentLoggedInUserId);
    //     calcAndSetUnreadCount();
    // }
    // const onClickClearAll = () => {
    //     clearAllCounts();
    // }
    const calcAndSetUnreadCount = () => {
        let n = getNumUnreadMsgs(currentLoggedInUserId, user.id);
        console.log("***** "  + n + " **** " + areViewingUser);
        setUnreadCount(n);
    }
      
  //--render--
  const render = () => {
    if (user == null || unreadCount == 0 ) {return null};

    let numberStyle = {
        borderRadius: "50px",
        background: "red",
        color: "white",
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        width: "18px",
        height: "18px",
        position: "relative",
        top: "-5px",
        left: "-5px",
    }

    return <>
        {
            <span className="is-size-7" style={numberStyle}>&thinsp;{unreadCount}&thinsp;</span>
        }
    </>
  }

  return <>
     {render()}
  </>;
}


