
const days = ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'];
const months = ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'];

//output 3:05 pm
export const formatTimeOfDay = (dtParam) => {
    let dt = new Date(dtParam); //converts to local time of browser
    let hr = dt.getHours();
    let min = dt.getMinutes();
    let sec = dt.getSeconds();
    if (sec > 30) {
        min = min + 1;
    }
    var ap = dt.getHours() < 12 ? 'am' : 'pm';
    let str = dt.getHours() < 12 ? hr : hr - 12;

    if (min < 10) {
        str += ":0" + min + " " + ap;
    } else {
        str += ":" + min + " " + ap;
    }
    return str;
}

export const formatDateForDivider = (dtParam) => {
    let dt = new Date(dtParam); //converts to local time of browser
    //    let str = dt.toLocaleString('en-US'); //'en-GB', { timeZone: 'UTC' }
    let mon = dt.getMonth();
    let day = dt.getDay();
    let date = dt.getDate();

    let suffix = 'th';
    if (date == 1) {
        suffix = 'st';
    }
    if (date == 2) {
        suffix = 'nd';
    }
    if (date == 3) {
        suffix = 'rd';
    }
    let str = days[day] + ", " + months[mon] + " " + date + suffix;
    // let str = "" + mon + " " + day;
    return str;
}

//ignores time of day and just compares yyyy,mm,dd
export const areSameDate = (dtStr1, dtStr2) => {
    let d1 = new Date(dtStr1).toDateString(); //"Wed Jul 28 1993"
    let d2 = new Date(dtStr2).toDateString();
    return d1 == d2;
}

export const formatCDate = (dt) => {
    //ISO Date	"2015-03-25" 
    //           0123456789
    if (dt == undefined) {
        dt = new Date();
    }

    var now = dt.toISOString().slice(0, 10);
    let str = now.substring(0, 4);
    str += now.substring(5, 7);
    str += now.substring(8, 10);
    return str;
}

export const convertCDateToDate = (cdate) => {
    let yr = cdate.substring(0,4);
    let mon = cdate.substring(4,6);
    let day = cdate.substring(6,8);

    let monIndex = parseInt(mon);
    console.log(mon + "," + monIndex);
    console.log(day);
    let dt = new Date(yr, monIndex - 1, day);
    return dt;
}

//cacl 1st of month for dtInitial date +/- numMonths
export const adjustDateByMonth = (dtInitial, numMonths) => {
    let dt = new Date(dtInitial.getFullYear(), dtInitial.getMonth() + numMonths, 1);
    return dt;
}



