//Types of environments:
//    "loc":              local dev. mock all API calls
//    "prod":             running in prod environment inside the java web app

//let envType = "loc";
let envType = "prod";  //<---- uncomment this for prod or qa build or to test locally with AWS
if (window.location.port === "3000") { //running separately in 'npm start'?
  envType = "loc";
}


// --- functions -----
const getEnvType = () => {
  return envType;
}

const getBaseUrl = () => {
  let baseUrl = window.location.protocol + '//' + window.location.hostname;
  if (envType === "loc" || window.location.port === "8080") {
      baseUrl = baseUrl + ":8080"; //'http://localhost:8080' 
  }
  return baseUrl;
}

//these methods can be altered temporarily during development to enable various i/o calls to ezclocker or AWS

//whether to call EZClocker java app
const shouldMockServerCalls = () => {
  let flag = false;
  switch (envType) {
    case "loc":
      flag = true;
      break;
    case "prod":
      break;
    default:
      break;
  }
  return flag;
}
