import React, { useState, useEffect, useRef } from "react";
import { doEzClockerAPIPOST } from '../api/ServerAPI';
import { API,graphqlOperation } from 'aws-amplify';
import { listUsers } from '../graphql/queries';
import { createEmployee, createUser, updateEmployee, deleteEmployee,deleteUser,updateUser } from '../graphql/mutations';
import { shouldMockServerCalls, shouldMockSyncUsersOnAWS } from '../environment';
import { parseEmployerId,parseEmployeeId } from "../util/CGIParamUtil";

export const EzClockerSyncComponent = ({ employer, employees, isEmployer, debugFlag, setIsReady }) => {
    const [commError, setCommError] = useState(false);
    const onceFlag = useRef(false)
    const [syncStarted, setSyncStarted] = useState(false);
    const [syncFin, setSyncFin] = useState(false);

    let mockFlag = shouldMockServerCalls(); //when we run the react app locally you need to set this to true
    let mockAWSFlag = shouldMockSyncUsersOnAWS(); //when we run the react app locally you need to set this to true

    useEffect(() => {
        if (!isEmployer) {
            console.log("init EzClockerSyncComponent (employee)...");
            setIsReady(true);
            return;
        }
        console.log("init EzClockerSyncComponent...");

        // declare the data fetching function
        const fetchData = async () => {
            try {
                // start:: prevent executing useEffect twice
                if (onceFlag.current) {
                    return
                }
                onceFlag.current = true
                // end:: prevent executing useEffect twice

                console.log("start-sync");
                setSyncStarted(true);
                let req = buildReq();
                const resp = await getEzClockerSyncActions(req);

                console.log("======================> xdo Sync!!");
                const resp2 = await execSyncActions(resp);

                setSyncFin(true);
                console.log("start-sync done");
                setIsReady(true);
            } catch (error) {
                console.log("COMM-ERRRRRRRRRRRR");
                console.log(error);
                setCommError(true);
            }
        };

        fetchData();
        //console.log("StartupComponent init done");
    }, []);

    const buildReq = () => {
        let ar = [];
        for(let i = 0; i < employees.length; i++) {
            let emp = employees[i];
            console.log(emp);
            let obj = {
                guid: emp.id,
                userName: emp.userName,
                email: emp.email
            }
            ar.push(obj);
        }
        console.log("xxreq #employees= ", ar.length);
        console.log(ar);

        let result = {
            employees: ar
        }
        return result;
    }

    //--click--
    // const clkSync = async () => {
    //     console.log("sync..");
    //     try {
    //         const resp2 = await getEzClockerSyncData();
    //     } catch (error) {
    //         console.log("ERRORxx ");
    //         console.log(error);
    //    }
    // }

    const getEzClockerSyncActions = async (body) => {
        if (mockFlag) {
            console.log("MOCKcall to ezclocker!!")
            let obj = {
                inserts: [],
                updates: [],
                deletes: []
            }

            return obj;
        }

        console.log("getEzClockerSyncData..");
        let employerId = parseEmployerId();
        let url = `/teamchat/sync?employerId=${employerId}`;
        const json = await doEzClockerAPIPOST(url, body);
        return json;
    }

    const execSyncActions = async (resp) => {
        // inserts
        console.log("execSyncActions:inserts " + resp.inserts.length);
        for (let i = 0; i < resp.inserts.length; i++) {
            let empInfo = resp.inserts[i];
            const tmp = await doCreateEmployee(empInfo);
            const tmp2 = await doCreateUser(empInfo);
        }
        console.log("execSyncActions:inserts END");

        // updates
        console.log("execSyncActions:updates " + resp.updates.length);
        for (let i = 0; i < resp.updates.length; i++) {
            let empInfo = resp.updates[i];
            const tmp = await doUpdateEmployee(empInfo);
            const user = await getAWSUser(empInfo);
             if (user) {
                const tmp2 = await doUpdateEmployeeUser(user, empInfo);
            }
        }
        console.log("execSyncActions:updates END");

        // deletes
        console.log("execSyncActions:deletes " + resp.deletes.length);
        for (let i = 0; i < resp.deletes.length; i++) {
            let empInfo = resp.deletes[i];
            const tmp = await doDeleteEmployee(empInfo);
            const user = await getAWSUser(empInfo);
             if (user) {
                const tmp2 = await doDeleteEmployeeUser(user);
            }
        }
        console.log("execSyncActions:deletes END");

        return {};
    }

    // -- graphql ---
    const doCreateEmployee = async (empInfo) => {
        console.log("createEmployee " + empInfo.userName);
        let obj = {
            id: empInfo.guid,
            userName: empInfo.userName,
            email: empInfo.email,
            employerID: employer.id,
        };
  
        const newObj = await API.graphql(graphqlOperation(createEmployee, { input: obj }));
        console.log("createEmployee-back");
    }
    const doCreateUser = async (empInfo) => {
        console.log("createUser " + empInfo.userName);
        let obj = {
            userName: empInfo.userName,
            employerID: employer.id,
            employeeID: empInfo.guid,
            viewedUsers: null,
            viewedMsgMap: null         
        };

        const newObj = await API.graphql(graphqlOperation(createUser, { input: obj }));
        console.log("createUser-back");
    }
    const doUpdateEmployee = async (empInfo) => {
        console.log("updateEmployee " + empInfo.userName);
        const newObj = await API.graphql(graphqlOperation(updateEmployee, 
            { input: { id: empInfo.guid, userName: empInfo.userName, email: empInfo.email } }));
        console.log("updateEmployee-back");
    }
    const doUpdateEmployeeUser = async (user, empInfo) => {
        console.log("updateEmployeeUser " + user.id);
        //only updating a single field
        let obj = {
            id: user.id, 
            userName: empInfo.userName
        }
        const newObj = await API.graphql(graphqlOperation(updateUser, { input: obj }));
        console.log("updateEmployeeUser-back");
    }
    const doDeleteEmployee = async (empInfo) => {
        console.log("deleteEmployee " + empInfo.userName);
        const newObj = await API.graphql(graphqlOperation(deleteEmployee, 
            { input: { id: empInfo.guid } }));
        console.log("deleteEmployee-back");
    }
    const doDeleteEmployeeUser = async (user) => {
        console.log("deleteEmployeeUser " + user.id);
        const newObj = await API.graphql(graphqlOperation(deleteUser, 
            { input: { id: user.id } }));
        console.log("deleteEmployeeUser-back");
    }
    const getAWSUser = async (empInfo) => {
        console.log("calling-getUserBB " + empInfo.guid);
        const params = {
            limit: 20, //TODO is there a max???
            sortDirection: 'DESC'      
          };       
        const filter = {employeeID: {eq: empInfo.guid}};
        const newObj = await API.graphql(graphqlOperation(listUsers, { filter:filter, input: params }));
        let ar = newObj.data.listUsers.items;
        console.log("back-getUser.............");
        console.log(ar);
        if (ar && ar.length > 0) {
            return ar[0];  //should only be one
        }
        return null;
    }


    //--calc--


    //--render--
    const renderFlags = () => {
        if (!syncStarted) return null;
        return <span>
            Flags: {syncStarted ? 'STARTED' : ''}, {syncFin ? 'FIN' : 'RUNNING'}
            &nbsp; {renderCommError()}
        </span>
    }
    const renderCommError = () => {
        if (commError) {
            return <>
                COMM-ERROR!
            </>
        }
        return null;
    }

    return <>
        { debugFlag && (
            <div>
           sync-comp: {renderFlags()} 
            </div>
        )}
    </>;
}


