
export const parseEmployerId = () => {
    const queryString = window.location.search;
    const params = new URLSearchParams(queryString);
    const q = params.get("employerId");
    return q;
}
export const parseEmployeeId = () => {
    const queryString = window.location.search;
    const params = new URLSearchParams(queryString);
    const q = params.get("employeeId");
    return q;
}

