//a very simple data store that can be used between react components
//we store msgs for all employees here
//Generally we store a couple of days worth, using a single query to AWS (see MessageStoreComponent)
import { setListForUnreadMsgCalc, findMessagesForUserInGroupConversationEx, findMessagesForUserEx, getSortedMsgArray } from './UnreadMsgTracker';

var storedMessagesArray = [];
var currentLoggedInUserId = null;
var currentChannelID;
var mostRecentSentMsg = null;
//var idMap = new Map();

//ensure only messages for the current channel are shown
//we sometimes receive messages from other channels.
//The channelID is the employer.id (or in ezlcocker it's employer.guid)
export const setCurrentChannelID = (id) => {
    currentChannelID = id;
    console.log("MessageStore:currentChannelID: " + currentChannelID);
}
export const getCurrentChannelID = () => {
    return currentChannelID;
}

export const setCurrentLoggedInUserId = (loggedInUserId) => {
    currentLoggedInUserId = loggedInUserId;
    console.log("currentLoggedInUserId: " + currentLoggedInUserId);
}

export const getMessageStoreCount = () => {
    return storedMessagesArray.length;
}

//the main fn called when we send a msg
export const setMostRecentSentMsg = (msg) => {
    mostRecentSentMsg = msg;
    console.log("mostRecentSentMsg: " + msg.body);
}


//the main fn called when msgs are loaded
export const appendRawMessagesAndReCalc = (msgsParam) => {
    appendRawMessages(msgsParam);
    reCalcUnreadMsgs();
}
export const reCalcUnreadMsgs = () => {
    //console.log("aareCalcUnreadMsgs " + storedMessagesArray.length);
    setListForUnreadMsgCalc(storedMessagesArray);
}
//a lower-level fn called when a msg is loaded or sent
export const appendRawMessages = (msgsParam) => {
    //filter, so we only use msgs whose channel is currentChannelID
    let msgs = [];
    for(let i = 0; i < msgsParam.length; i++) {
        if (msgsParam[i].channelID == currentChannelID) {
            msgs.push(msgsParam[i]);
        }
    }
    //now msgs only contains msgs in currentChannelID
    // console.log("LEN " + msgsParam.length);
    // console.log("LEx " + msgs.length);

    console.log("appendMM: " + msgs.length);
    // //call observer
    // for(let i = 0; i < msgs.length; i++) {
    //     onObserveReceivedMsg(msgs[i], currentLoggedInUserId);
    // }

    let newArray = [];
    for(let i = 0; i < storedMessagesArray.length; i++) {
        let msg = storedMessagesArray[i];
        let id = msg.id;

        const result = msgs.filter((x) => x.id === id);
        //console.log("x " + id + ", " + result.length);
        if (result.length > 0) { //id exists already in store and in incoming msgs?
            newArray.push(result[0]); //use new msg
            msgs = msgs.filter((x) => x.id !== id); //remove id element
            //console.log("zz" + msgs.length);
        } else { //id not in msgs
            newArray.push(msg); //add it
        }
    }

    for(let i = 0; i < msgs.length; i++) {
        let msg = msgs[i];
        handleMostRecentSentMsgIfNeeded(msg);
        newArray.push(msg); //use new msg
    }
    storedMessagesArray = newArray;

    console.log("......................xappendMMM " + storedMessagesArray.length);
}

const handleMostRecentSentMsgIfNeeded = (msg) => {
    if (mostRecentSentMsg) {
        // console.log("mac: ");
        // console.log(msg);
        //when MessageSender sent the message it didn't have msg.id because AWS assigns that
        //so we must compare all the fields
        if ((msg.cdate == mostRecentSentMsg.cdate) &&
           (msg.fromUser == mostRecentSentMsg.fromUser) &&
           (msg.toUser == mostRecentSentMsg.toUser) && 
           (msg.body == mostRecentSentMsg.body)) {
            console.log("ddCaughtIt");
            msg.haveRead = true;
            mostRecentSentMsg = null; //clear latch
        }
    }
}


export const getRawMessages = () => {
    console.log("getMMM");
    return storedMessagesArray;
}

export const deleteRawMessage = (doomedMsg) => {
    let newArray = [];
    for(let i = 0; i < storedMessagesArray.length; i++) {
        let msg = storedMessagesArray[i];
        if (doomedMsg.id == msg.id ) {  //do nothing
            console.log("DELETE me: " + msg.id);
        } else { 
            newArray.push(msg); //use new msg
        }
    }
    storedMessagesArray = newArray;
}

export const findMessagesForUser = (currentLoggedInUserId, userId) => {
    let ar = getSortedMsgArray();
    return findMessagesForUserEx(ar, currentLoggedInUserId, userId);
}

//here userId is '*'
export const findMessagesForUserInGroupConversation = (userId) => {
    let ar = getSortedMsgArray();
    return findMessagesForUserInGroupConversationEx(ar, userId);
}

